<template>
    <div>

        <div >
            <vs-divider>
                Список групп
            </vs-divider>

            <vs-row>
                <vs-col type="flex" vs-justify="center" vs-align="center" vs-sm="12" vs-lg="3" v-for="(section, index) in $store.state.modules.sections">
                    <vs-card @contextmenu.prevent.native="$refs.menu.open($event, section)" actionable @click.native="selectSection(section.id)">
                        <div class="card-content-section">
                            <h5 class="mb-1">{{ section.title }}</h5>
                            Кол-во модулей: {{ !section.modules ? 0 : section.modules.length }}шт.
                        </div>
                    </vs-card>
                </vs-col>

                <vs-col type="flex" vs-justify="center" vs-align="center" vs-sm="12" vs-lg="3">
                    <vs-card actionable @click.native="addSection">
                        <div class="card-content-section" >
                            <h5>+ Добавить группу</h5>
                        </div>
                    </vs-card>
                </vs-col>
            </vs-row>
        </div>

        <vs-prompt
                vs-title="Новая группа"
                vs-accept-text="Добавить"
                vs-cancel-text="Отменить"
                @vs-cancel="cancelAddSection"
                @vs-accept="saveSection"
                @vs-close="cancelAddSection"
                :vs-active.sync="dialogAddSection">
            <div class="con-exemple-prompt">
                <span>Введите наименование группы</span>
                <vs-input placeholder="Группа 1" vs-placeholder="Группа 1" v-model="newSectionTitle" class="mt-3 w-full" />
            </div>
        </vs-prompt>

        <vs-prompt
                vs-title="Редакитровать группу"
                vs-accept-text="Сохранить"
                vs-cancel-text="Отменить"
                @vs-cancel="cancelAddSection"
                @vs-accept="saveUpdateSection"
                @vs-close="cancelAddSection"
                :vs-active.sync="dialogUpdateSection">
            <div class="con-exemple-prompt">
                <span>Измените наименование группы</span>
                <vs-input placeholder="Группа 1" vs-placeholder="Группа 1" v-model="newSectionTitle" class="mt-3 w-full" @keyup.enter="saveUpdateSection"/>
            </div>
        </vs-prompt>

        <vue-context ref="menu">
            <template slot-scope="child">
                <ul class="bordered-items p-0">
                    <li @click="optionClicked($event.target.innerText, child.data)">Редактировать</li>
                    <li @click="optionClicked($event.target.innerText, child.data)">Удалить</li>
                </ul>
            </template>
        </vue-context>

    </div>
</template>

<script>
    import { VueContext } from 'vue-context';

    export default {
        components: { VueContext },
        data(){
            return {
                dialogAddSection: false,
                dialogUpdateSection: false,
                currentEditSection: '',
                newSectionTitle: ''
            }
        },
        methods: {
            optionClicked(text, data) {
                if(text === 'Редактировать'){
                    this.editSection(data);
                }else{
                    this.$vs.dialog({
                        type: 'confirm',
                        color: 'danger',
                        title: `Подтвердите удаление`,
                        text: 'Внимание! При удалении группы все ее модули будут удаленны!',
                        acceptText: 'Удалить',
                        cancelText: 'Отменить',
                        accept: this.deleteSection
                    })
                }
            },
            selectSection(index){
                this.$router.push('/modules/' + index)
            },
            addSection(){
                this.dialogAddSection = true;
            },
            saveSection(){
                this.$http.post('/sections', {title: this.newSectionTitle}).then((data) => {
                    this.$vs.notify({title:'Успешно!',text:'Группа сохраненна.',color:'success'});
                }).catch((e) => {
                    this.$vs.notify({title: 'Ошибка!', text: e.response.data.err, color: 'red'});
                });

                this.newSectionTitle = '';
            },
            cancelAddSection(){
                this.dialogAddSection = false;
                this.newSectionTitle = '';
                this.currentEditSection = '';
                this.dialogUpdateSection = false;
            },
            editSection(section){
                this.dialogUpdateSection = true;
                this.newSectionTitle = section.title;
                this.currentEditSection = section.id;
            },
            saveUpdateSection(){
                this.$http.post('/sections/' + this.currentEditSection, {title: this.newSectionTitle}).then((data) => {
                    this.$vs.notify({title:'Успешно!',text:'Группа сохраненна.',color:'success'});
                }).catch((e) => {
                    this.$vs.notify({title: 'Ошибка!', text: e.response.data.err, color: 'red'});
                }).then(() => {
                    this.cancelAddSection();
                });
            },
            deleteSection(id){
                console.log('DELETE ', id)
            }
        }
    }
</script>

<style scoped>
    .card-content-section{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 40px;
    }
</style>